import { PostHog } from "posthog-node";
import { createServerClient } from "./client";
import { allEvents } from "./schemas";

/**
 * Required context for exception tracking.
 * Ensures consistent error logging across all packages.
 */
export type ExceptionContext = {
  /** Specific error identifier (e.g., "sync_events_upload_failed", "analysis_llm_timeout") */
  error_type: string;
  /** Category for grouping errors (e.g., "sync", "analysis", "extraction") */
  error_category: string;
  /** Source location where the error originated (e.g., "daemon/sync-cycle", "extension/supabase-chats") */
  error_source: string;
} & Record<string, unknown>;

/**
 * Initialize the server-side analytics client
 * This should be called once with your PostHog API key
 */
export function createServerAnalytics(posthogApiKey: string) {
  const posthog = new PostHog(posthogApiKey, {
    host: "https://us.i.posthog.com",
    disableGeoip: false,
  });

  return {
    ...createServerClient(
      allEvents,
      (distinctId, event, properties) => {
        posthog.capture({
          distinctId,
          event,
          properties,
        });
      },
      () => posthog.shutdown(),
    ),
    /**
     * Capture an exception for error tracking.
     * @param error - The error to capture
     * @param distinctId - Optional user identifier
     * @param context - Required context with error_type, error_category, and error_source
     */
    captureException: (error: Error, distinctId: string | undefined, context: ExceptionContext) => {
      posthog.captureException(error, distinctId, context);
    },
  };
}
