import { z } from "zod";

export const analysisEvents = {
  standupGenerated: {
    name: "Standup Generated",
    schema: z.object({
      email: z.email().optional(),
      workspaceId: z.uuid(),
      workspaceName: z.string(),
      promptId: z.string().optional(),
      promptName: z.string().optional(),
      isRegeneration: z.boolean().optional(),
      isAutoGenerated: z.boolean().optional(),
      $ip: z.string().optional(),
    }),
  },
  teamStandupGenerated: {
    name: "Team Standup Generated",
    schema: z.object({
      email: z.email().optional(),
      workspaceId: z.uuid(),
      workspaceName: z.string(),
      teamId: z.uuid(),
      teamName: z.string(),
      promptId: z.string().optional(),
      promptName: z.string().optional(),
      usedCustomPrompt: z.boolean().optional(),
      hasFocusAreas: z.boolean().optional(),
      isRegeneration: z.boolean().optional(),
      standupsAggregated: z.number().optional(),
      $ip: z.string().optional(),
    }),
  },
} as const;
