/**
 * Get language ID from file path based on extension.
 * Supports both formats: with or without leading dot in extension.
 *
 * @param filePath - The file path to detect language from
 * @returns The language ID (e.g., "typescript", "python") or "plaintext" if unknown
 *
 * @example
 * ```ts
 * getLanguageFromPath("src/index.ts") // => "typescript"
 * getLanguageFromPath("script.py") // => "python"
 * getLanguageFromPath("README.md") // => "markdown"
 * ```
 */
export function getLanguageFromPath(filePath: string): string {
  // Extract extension without the dot
  const ext = filePath.split(".").pop()?.toLowerCase();
  return languageMap[ext || ""] || "plaintext";
}

/**
 * Alias for getLanguageFromPath for backward compatibility
 * @deprecated Use getLanguageFromPath instead
 */
export function detectLanguageId(filePath: string): string {
  return getLanguageFromPath(filePath);
}
