import type { z } from "zod";

// Type for event schema definition
export type EventSchema = {
  name: string;
  schema: z.ZodType<any>;
};

// Type for a collection of event schemas
export type EventSchemas = Record<string, EventSchema>;

// Extract properties type from an event schema
type EventProperties<T extends EventSchema> = z.infer<T["schema"]>;

// Helper: Shared validation logic
function validateEvent<T extends EventSchemas>(schemas: T, eventName: keyof T, properties: any) {
  const eventSchema = schemas[eventName];
  if (!eventSchema) {
    throw new Error(`Unknown event: ${String(eventName)}`);
  }

  const validationResult = eventSchema.schema.safeParse(properties);
  if (!validationResult.success) {
    throw new Error(
      `Invalid properties for event "${String(eventName)}": ${validationResult.error.message}`,
    );
  }

  return { name: eventSchema.name, data: validationResult.data };
}

// --- Browser Client Types & Factory ---

type BrowserTrackParams<T extends EventSchemas> = {
  [K in keyof T]: {
    event: K;
    properties: EventProperties<T[K]>;
  };
}[keyof T];

export function createBrowserClient<T extends EventSchemas>(
  schemas: T,
  captureFunc: (event: string, properties: any) => void | Promise<void>,
  disposeFunc?: () => void | Promise<void>,
) {
  return {
    track: (params: BrowserTrackParams<T>) => {
      const { name, data } = validateEvent(schemas, params.event, params.properties);
      return captureFunc(name, data);
    },
    dispose: disposeFunc || (() => Promise.resolve()),
  };
}

// Export the browser client type for consumers
export type BrowserSchemaClient<T extends EventSchemas> = ReturnType<typeof createBrowserClient<T>>;

// --- Server Client Types & Factory ---

type ServerTrackParams<T extends EventSchemas> = {
  [K in keyof T]: {
    distinctId: string; // Mandatory for server
    event: K;
    properties: EventProperties<T[K]>;
  };
}[keyof T];

export function createServerClient<T extends EventSchemas>(
  schemas: T,
  captureFunc: (distinctId: string, event: string, properties: any) => void | Promise<void>,
  disposeFunc?: () => void | Promise<void>,
) {
  return {
    track: (params: ServerTrackParams<T>) => {
      const { name, data } = validateEvent(schemas, params.event, params.properties);
      return captureFunc(params.distinctId, name, data);
    },
    dispose: disposeFunc || (() => Promise.resolve()),
  };
}

// Export the server client type for consumers
export type ServerSchemaClient<T extends EventSchemas> = ReturnType<typeof createServerClient<T>>;
