import posthog, { type PostHogConfig } from "posthog-js";
import { createBrowserClient } from "./client";
import { allEvents } from "./schemas";
import type { ExceptionContext } from "./server";

let isInitialized = false;

/**
 * Initialize the browser-side analytics client
 * This should be called once with your PostHog API key
 */
export function initializeBrowserAnalytics(
  posthogApiKey: string,
  options?: Partial<PostHogConfig>,
) {
  if (isInitialized) {
    return;
  }
  posthog.init(posthogApiKey, {
    api_host: options?.api_host || "https://us.i.posthog.com",
    defaults: "2025-05-24",
  });
  isInitialized = true;
}

/**
 * Initialize the browser-side analytics client
 * This should be called once with your PostHog API key
 */
export function createBrowserAnalytics() {
  if (!isInitialized) {
    console.warn(
      "Browser analytics not initialized. Please call initializeBrowserAnalytics first.",
    );
  }

  return {
    ...createBrowserClient(
      allEvents,
      (event, properties) => {
        posthog.capture(event, properties);
      },
      () => {
        // Browser client doesn't need explicit disposal
        // but we can flush events if needed
        posthog.capture("$flush");
      },
    ),
    /**
     * Identify the current user
     */
    identify: (userId: string, properties?: Record<string, any>) => {
      posthog.identify(userId, properties);
    },
    /**
     * Reset the user identification (e.g., on logout)
     */
    reset: () => {
      posthog.reset();
    },
    /**
     * Capture an exception for error tracking.
     * @param error - The error to capture
     * @param context - Required context with error_type, error_category, and error_source
     */
    captureException: (error: Error, context: ExceptionContext) => {
      posthog.captureException(error, context);
    },
  };
}
