/**
 * Shared extraction helper functions for hook handlers
 *
 * Provides reusable logic for finding sessions, extracting data, and queueing events
 * Integrates with privacy manager to ensure data is redacted before queueing
 */

import { randomUUID } from "node:crypto";
import { realpath, stat } from "node:fs/promises";
import { basename, join } from "node:path";
import { getLanguageFromPath } from "@zest/utils";
import { getProjectInfoSync } from "@zest/utils/node";
import { EXTRACTION_PROJECT_DIR_NOT_FOUND } from "../analytics/events.js";
import { captureException } from "../analytics/index.js";
import { getValidSession } from "../auth/session-manager.js";
import { CLAUDE_DIR_SEPARATOR_PATTERN, CLAUDE_PROJECTS_DIR } from "../config/constants.js";
import { loadSettings } from "../config/settings.js";
import { extractNewMessagesFromFile } from "../extractors/message-parser.js";
import { getPrivacyManager } from "../privacy/privacy-manager.js";
import type { ToolUse } from "../types/extractors.js";
import type {
  ClaudeExtractedEvent,
  ClaudeExtractedMessage,
  ClaudeExtractedSession,
} from "../types/index.js";
import { shouldExcludeCommand } from "./command-filters.js";
import { logger } from "./logger.js";
import { enqueueChatMessage, enqueueChatSession, enqueueEvent } from "./queue-manager.js";
import { readSessionState, updateLastReadLine } from "./state-manager.js";

/**
 * File stats from fs.stat()
 */
interface FileStats {
  birthtime: Date;
  mtime: Date;
  mtimeMs: number;
  size: number;
}

/**
 * Ensure privacy manager is initialized for the given project.
 * Called before extraction to ensure redaction is applied during queuing.
 */
async function ensurePrivacyInitialized(projectDir: string): Promise<void> {
  const privacyManager = getPrivacyManager();
  if (!privacyManager.isInitialized()) {
    try {
      const settings = await loadSettings();
      await privacyManager.initialize(projectDir, settings.privacy);
      logger.debug("Privacy manager initialized for extraction");
    } catch (error) {
      // Log but don't fail - extraction can proceed without privacy
      logger.warn("Failed to initialize privacy manager for extraction:", error);
    }
  }
}

/**
 * Find the most recent Bash command from JSONL file
 * Used by PreToolUse hook to detect rm commands before they execute
 */
export async function findRecentBashCommand(conversationFile: string): Promise<string | null> {
  try {
    const { readFile } = await import("node:fs/promises");
    const content = await readFile(conversationFile, "utf-8");
    const lines = content
      .trim()
      .split("\n")
      .filter((l) => l.trim());

    // Look for the last Bash tool_use in recent lines (last 5 lines)
    for (let i = lines.length - 1; i >= Math.max(0, lines.length - 5); i--) {
      try {
        const entry = JSON.parse(lines[i]);
        if (entry.message?.content && Array.isArray(entry.message.content)) {
          for (const block of entry.message.content) {
            if (block.type === "tool_use" && (block.name === "Bash" || block.name === "bash")) {
              const command = block.input?.command || block.input?.cmd;
              if (command) {
                // Filter out excluded commands
                if (shouldExcludeCommand(command)) {
                  logger.debug(
                    `Filtered out excluded bash command: ${command.substring(0, 50)}...`,
                  );
                  return null;
                }
                return command;
              }
            }
          }
        }
      } catch {
        // Skip invalid lines
      }
    }

    return null;
  } catch (error) {
    logger.debug("Failed to find recent Bash command:", error);
    return null;
  }
}

/**
 * Parse Bash command to detect file deletion operations
 */
export function parseRmCommand(command: string): string[] {
  if (!command) return [];

  const cmd = command.trim();

  // Match rm commands: rm, rm -rf, rm -f, etc.
  // Pattern: rm [options] filepath(s)
  const rmMatch = cmd.match(/^rm\s+(?:-[a-zA-Z]+\s+)*(.+)$/);
  if (rmMatch) {
    // Extract file paths (may be multiple space-separated paths)
    const pathsString = rmMatch[1].trim();
    // Split by spaces but respect quotes
    const paths =
      pathsString.match(/(?:[^\s"']+|"[^"]*"|'[^']*')+/g)?.map((p) => p.replace(/['"]/g, "")) || [];
    return paths;
  }

  return [];
}

/**
 * Cache multiple files for deletion
 * Used by PreToolUse hook to store file content before rm execution
 */
export async function cacheFilesForDeletion(
  filePaths: string[],
  sessionId: string,
  projectDir: string,
): Promise<void> {
  const { readFile } = await import("node:fs/promises");
  const { resolve } = await import("node:path");
  const { cacheFileForDeletion } = await import("./deletion-cache.js");

  for (const filePath of filePaths) {
    try {
      // Resolve to absolute path, using project directory as base for relative paths
      const absolutePath = filePath.startsWith("/") ? filePath : resolve(projectDir, filePath);

      // Read file content
      const content = await readFile(absolutePath, "utf-8");

      // Cache the content with absolute path
      await cacheFileForDeletion(absolutePath, content, sessionId);
    } catch (error) {
      // File might not exist or not readable
      logger.debug(`Could not cache ${filePath}:`, error);
    }
  }
}

/**
 * Find the most recent conversation file for a project
 */
export async function findConversationFile(
  projectDir: string,
): Promise<{ conversationFile: string; sessionId: string; fileStats: FileStats } | null> {
  try {
    // Resolve symlinks to match Claude Code's behavior (e.g. /tmp -> /private/tmp on macOS)
    let resolvedDir: string;
    try {
      resolvedDir = await realpath(projectDir);
    } catch {
      resolvedDir = projectDir;
    }

    // Convert CWD to Claude's directory naming convention
    const claudeDirName = resolvedDir.replace(CLAUDE_DIR_SEPARATOR_PATTERN, "-");

    let projectPath = join(CLAUDE_PROJECTS_DIR, claudeDirName);

    logger.debug(`Looking for project directory: ${projectPath}`);

    // Check if directory exists
    try {
      await stat(projectPath);
    } catch {
      captureException(
        new Error(`Project directory not found: ${claudeDirName}`),
        EXTRACTION_PROJECT_DIR_NOT_FOUND,
        "extraction-helpers",
        {
          computed_dir_name: claudeDirName,
          resolved_project_dir: resolvedDir,
          original_project_dir: projectDir,
          os_platform: process.platform,
        },
      );
      logger.warn(`Project directory not found: ${projectPath}`);
      return null;
    }

    // Find the most recent JSONL file
    const { readdir } = await import("node:fs/promises");
    const entries = await readdir(projectPath);
    const jsonlFiles = entries.filter((f) => f.endsWith(".jsonl"));

    if (jsonlFiles.length === 0) {
      logger.warn(`No session files found in ${projectPath}`);
      return null;
    }

    // Get the most recently modified .jsonl file
    let mostRecentFile = jsonlFiles[0];
    let mostRecentTime = 0;

    for (const file of jsonlFiles) {
      const filePath = join(projectPath, file);
      const stats = await stat(filePath);
      if (stats.mtimeMs > mostRecentTime) {
        mostRecentTime = stats.mtimeMs;
        mostRecentFile = file;
      }
    }

    const conversationFile = join(projectPath, mostRecentFile);
    const sessionId = basename(mostRecentFile, ".jsonl");
    const fileStats = await stat(conversationFile);

    return { conversationFile, sessionId, fileStats };
  } catch (error) {
    logger.error("Failed to find conversation file:", error);
    return null;
  }
}

/**
 * Extract new session data (messages and tool uses) from conversation file
 * Single Responsibility: Extraction only
 */
export async function extractNewSessionData(
  conversationFile: string,
  sessionId: string,
): Promise<{
  messages: ClaudeExtractedMessage[];
  toolUses: ToolUse[];
  newLastReadLine: number;
  lastMessageIndex: number;
  isNewSession: boolean;
  hasNewData: boolean;
}> {
  // Read extraction state
  const state = await readSessionState(sessionId);
  const lastReadLine = state?.lastReadLine || 0;
  const previousLastMessageIndex = state?.lastMessageIndex ?? -1; // Start at -1 so first message is 0
  const isNewSession = !state;

  // Extract only new messages/tool uses, starting from the next message index
  const { messages, toolUses, newLastReadLine, lastMessageIndex } =
    await extractNewMessagesFromFile(
      conversationFile,
      sessionId,
      lastReadLine,
      previousLastMessageIndex + 1, // Start numbering from the next index
    );

  const hasNewData = messages.length > 0 || toolUses.length > 0;

  return {
    messages,
    toolUses,
    newLastReadLine,
    lastMessageIndex,
    isNewSession,
    hasNewData,
  };
}

/**
 * Queue extracted session data (session info, messages, and events)
 * Single Responsibility: Queuing only
 * Ensures privacy manager is initialized before queuing to apply redaction
 */
export async function queueSessionData(
  sessionId: string,
  messages: ClaudeExtractedMessage[],
  toolUses: ToolUse[],
  fileStats: FileStats,
  projectDir: string,
  conversationFile: string,
  newLastReadLine: number,
  lastMessageIndex: number,
  isNewSession: boolean,
): Promise<{ messagesQueued: number; eventsQueued: number }> {
  // Ensure privacy is initialized before queuing (redaction happens in queue functions)
  await ensurePrivacyInitialized(projectDir);

  // Queue the session info (first time only)
  // Project info is captured HERE at extraction time when projectDir (CLAUDE_PROJECT_DIR) is available.
  // This is critical because the daemon runs from the plugin directory, not the user's project.
  if (isNewSession) {
    const projectInfo = getProjectInfoSync(projectDir);
    const session: ClaudeExtractedSession = {
      id: sessionId,
      title: messages.length > 0 ? messages[0].content.substring(0, 100) : `Session ${sessionId}`,
      created_at: fileStats.birthtime.toISOString(),
      project_id: projectInfo.projectId !== "unknown" ? projectInfo.projectId : null,
      project_name: projectInfo.projectName !== "unknown" ? projectInfo.projectName : null,
    };
    await enqueueChatSession(session);
  }

  // Queue all messages (minimal extracted data)
  for (const message of messages) {
    const extractedMessage: ClaudeExtractedMessage = {
      id: message.id,
      session_id: message.session_id,
      message_index: message.message_index,
      role: message.role,
      content: message.content,
      created_at: message.created_at,
      metadata: message.metadata,
    };
    await enqueueChatMessage(extractedMessage);
  }

  // Convert tool uses to code digest events with filtering
  const eventsQueued = await queueToolUseEvents(toolUses, sessionId, projectDir);

  // Update state with new line number and last message index
  await updateLastReadLine(sessionId, conversationFile, newLastReadLine, lastMessageIndex);

  return { messagesQueued: messages.length, eventsQueued };
}

/**
 * Queue tool use events (with filtering for file edits with diffs)
 */
async function queueToolUseEvents(
  toolUses: ToolUse[],
  sessionId: string,
  projectDir: string,
): Promise<number> {
  // FILTER: Only include file edits with actual code diffs (exclude Read operations and edits without diffs)
  // Get workspace ID from user's session
  const session = await getValidSession();
  const workspaceId = session?.workspaceId || null;

  let queuedEventCount = 0;

  for (const toolUse of toolUses) {
    // Skip if no file path
    if (!toolUse.file_path) continue;

    // Skip Read operations (we only want file edits)
    if (toolUse.tool_name === "Read" || toolUse.tool_name === "read") {
      continue;
    }

    // Skip operations that don't have a diff (no actual code change)
    if (!toolUse.diff) {
      continue;
    }

    const event: ClaudeExtractedEvent = {
      id: randomUUID(),
      timestamp: toolUse.timestamp,
      document_uri: toolUse.file_path,
      language_id: getLanguageFromPath(toolUse.file_path),
      workspace_folder_uri: projectDir || null,
      session_id: sessionId,
      workspace_id: workspaceId,
      payload: {
        tool_name: toolUse.tool_name,
        session_id: sessionId,
        diff: toolUse.diff, // Already sanitized unified diff string
      },
    };

    await enqueueEvent(event);
    queuedEventCount++;
  }

  return queuedEventCount;
}
