/**
 * Local queue management for data persistence
 *
 * Manages JSONL queue files for events, sessions, and messages
 * Uses file-based locking to prevent corruption from concurrent access
 * Applies privacy redaction before enqueueing to protect sensitive data
 */

import { appendFile, readFile, stat, unlink, writeFile } from "node:fs/promises";
import { dirname } from "node:path";
import { QUEUE_READ_CORRUPTED, QUEUE_WRITE_FAILED } from "../analytics/events.js";
import { captureException } from "../analytics/index.js";
import { buildFileSystemProperties } from "../analytics/properties.js";
import {
  EVENTS_QUEUE_FILE,
  MESSAGES_QUEUE_FILE,
  MIN_MESSAGES_PER_SESSION,
  QUEUE_DIR,
  SESSIONS_QUEUE_FILE,
} from "../config/constants.js";
import { getPrivacyManager } from "../privacy/privacy-manager.js";
import type {
  ClaudeExtractedEvent,
  ClaudeExtractedMessage,
  ClaudeExtractedSession,
} from "../types/index.js";
import { withFileLock } from "./file-lock.js";
import { ensureDirectory } from "./fs-utils.js";
import { logger } from "./logger.js";

/**
 * Read all entries from a JSONL file
 */
async function readJsonl<T>(filePath: string): Promise<T[]> {
  try {
    const content = await readFile(filePath, "utf8");
    const lines = content.trim().split("\n").filter(Boolean);

    const results: T[] = [];
    let corruptedLines = 0;
    for (let i = 0; i < lines.length; i++) {
      try {
        results.push(JSON.parse(lines[i]));
      } catch (error) {
        logger.warn(`Failed to parse line ${i + 1} in ${filePath}:`, error);
        corruptedLines++;
        // Continue processing other lines
      }
    }
    // Track if we found corrupted lines (batch to avoid excessive events)
    if (corruptedLines > 0) {
      captureException(
        new Error(`Queue file has ${corruptedLines} corrupted lines`),
        QUEUE_READ_CORRUPTED,
        "queue-manager",
        {
          ...buildFileSystemProperties({ filePath, operation: "read" }),
          corrupted_lines: corruptedLines,
          total_lines: lines.length,
        },
      );
    }

    return results;
  } catch (error) {
    if ((error as NodeJS.ErrnoException).code === "ENOENT") {
      // File doesn't exist yet, return empty array
      return [];
    }
    throw error;
  }
}

/**
 * Count lines in a JSONL file
 */
async function countLines(filePath: string): Promise<number> {
  try {
    const content = await readFile(filePath, "utf8");
    const lines = content.trim().split("\n").filter(Boolean);
    return lines.length;
  } catch (error) {
    if ((error as NodeJS.ErrnoException).code === "ENOENT") {
      return 0;
    }
    throw error;
  }
}

/**
 * Delete a file (clear queue)
 */
async function deleteFile(filePath: string): Promise<void> {
  try {
    await unlink(filePath);
  } catch (error) {
    if ((error as NodeJS.ErrnoException).code === "ENOENT") {
      // File doesn't exist, nothing to delete
      return;
    }
    throw error;
  }
}

// ============================================================================
// Public API
// ============================================================================

/**
 * Enqueue a code digest event extracted from Claude Code CLI
 * Skips events that are already in the queue (deduplication)
 * Applies privacy redaction to diff content before enqueueing
 */
export async function enqueueEvent(event: ClaudeExtractedEvent): Promise<void> {
  try {
    // Apply privacy redaction to event diff content and check file exclusion
    const privacyManager = getPrivacyManager();

    // Skip if file should be excluded from telemetry
    if (event.document_uri && privacyManager.shouldExcludeFile(event.document_uri)) {
      logger.debug("Skipping event for excluded file", {
        documentUri: event.document_uri,
      });
      return;
    }

    // Redact sensitive data in diff content
    // Payload is typed as Json | null, so we need to safely handle it as an object
    let redactedPayload = event.payload;
    if (
      event.payload &&
      typeof event.payload === "object" &&
      !Array.isArray(event.payload) &&
      "diff" in event.payload &&
      typeof event.payload.diff === "string"
    ) {
      redactedPayload = {
        ...event.payload,
        diff: privacyManager.redactDiff(event.payload.diff, event.document_uri || undefined),
      };
    }

    const redactedEvent: ClaudeExtractedEvent = {
      ...event,
      payload: redactedPayload,
    };

    // Use lock to ensure atomicity of read-check-write
    await withFileLock(EVENTS_QUEUE_FILE, async () => {
      // Check if event already exists (inside lock to prevent race conditions)
      const existingEvents = await readJsonl<ClaudeExtractedEvent>(EVENTS_QUEUE_FILE);
      const isDuplicate = existingEvents.some((evt) => evt.id === redactedEvent.id);

      if (isDuplicate) {
        logger.debug("Skipping duplicate event", {
          eventId: redactedEvent.id,
          documentUri: redactedEvent.document_uri,
        });
        return;
      }

      // Ensure directory exists
      await ensureDirectory(dirname(EVENTS_QUEUE_FILE));

      // Append JSON line
      const line = JSON.stringify(redactedEvent) + "\n";
      await appendFile(EVENTS_QUEUE_FILE, line, "utf8");

      logger.debug("Enqueued event", {
        eventId: redactedEvent.id,
        documentUri: redactedEvent.document_uri,
      });
    });
  } catch (error) {
    logger.error("Failed to enqueue event:", error);
    throw error;
  }
}

/**
 * Enqueue a chat session extracted from Claude Code CLI
 * Skips sessions that are already in the queue (deduplication)
 * Applies privacy redaction to session title before enqueueing
 */
export async function enqueueChatSession(session: ClaudeExtractedSession): Promise<void> {
  try {
    // Apply privacy redaction to session title (derived from first message content)
    const privacyManager = getPrivacyManager();
    const redactedSession: ClaudeExtractedSession = {
      ...session,
      title: session.title ? privacyManager.redactMessage(session.title) : session.title,
    };

    // Use lock to ensure atomicity of read-check-write
    await withFileLock(SESSIONS_QUEUE_FILE, async () => {
      // Check if session already exists (inside lock to prevent race conditions)
      const existingSessions = await readJsonl<ClaudeExtractedSession>(SESSIONS_QUEUE_FILE);
      const isDuplicate = existingSessions.some((sess) => sess.id === redactedSession.id);

      if (isDuplicate) {
        logger.debug("Skipping duplicate session", { sessionId: redactedSession.id });
        return;
      }

      // Ensure directory exists
      await ensureDirectory(dirname(SESSIONS_QUEUE_FILE));

      // Append JSON line
      const line = JSON.stringify(redactedSession) + "\n";
      await appendFile(SESSIONS_QUEUE_FILE, line, "utf8");

      logger.debug("Enqueued session", { sessionId: redactedSession.id });
    });
  } catch (error) {
    logger.error("Failed to enqueue session:", error);
    throw error;
  }
}

/**
 * Enqueue a chat message extracted from Claude Code CLI
 * Skips messages that are already in the queue (deduplication)
 * Applies privacy redaction to message content before enqueueing
 */
export async function enqueueChatMessage(message: ClaudeExtractedMessage): Promise<void> {
  try {
    // Apply privacy redaction to message content
    const privacyManager = getPrivacyManager();
    const redactedMessage: ClaudeExtractedMessage = {
      ...message,
      content: privacyManager.redactMessage(message.content),
    };

    // Use lock to ensure atomicity of read-check-write
    await withFileLock(MESSAGES_QUEUE_FILE, async () => {
      // Check if message already exists (inside lock to prevent race conditions)
      const existingMessages = await readJsonl<ClaudeExtractedMessage>(MESSAGES_QUEUE_FILE);
      const isDuplicate = existingMessages.some((msg) => msg.id === redactedMessage.id);

      if (isDuplicate) {
        logger.debug("Skipping duplicate message", {
          messageId: redactedMessage.id,
          sessionId: redactedMessage.session_id,
          messageIndex: redactedMessage.message_index,
        });
        return;
      }

      // Ensure directory exists
      await ensureDirectory(dirname(MESSAGES_QUEUE_FILE));

      // Append JSON line
      const line = JSON.stringify(redactedMessage) + "\n";
      await appendFile(MESSAGES_QUEUE_FILE, line, "utf8");

      logger.debug("Enqueued message", {
        sessionId: redactedMessage.session_id,
        messageIndex: redactedMessage.message_index,
      });
    });
  } catch (error) {
    logger.error("Failed to enqueue message:", error);
    throw error;
  }
}

/**
 * Read all entries from a queue file
 */
export async function readQueue<T>(queueFile: string): Promise<T[]> {
  try {
    return await readJsonl<T>(queueFile);
  } catch (error) {
    logger.error(`Failed to read queue file ${queueFile}:`, error);
    throw error;
  }
}

/**
 * Write entries to a queue file (overwrites existing content)
 */
export async function writeQueue<T>(queueFile: string, items: T[]): Promise<void> {
  try {
    await withFileLock(queueFile, async () => {
      await ensureDirectory(dirname(queueFile));

      // Write all items as JSONL
      const content =
        items.map((item) => JSON.stringify(item)).join("\n") + (items.length > 0 ? "\n" : "");
      await writeFile(queueFile, content, "utf8");

      logger.debug(`Wrote ${items.length} items to queue file: ${queueFile}`);
    });
  } catch (error) {
    logger.error(`Failed to write queue file ${queueFile}:`, error);
    if (error instanceof Error) {
      captureException(error, QUEUE_WRITE_FAILED, "queue-manager", {
        ...buildFileSystemProperties({
          filePath: queueFile,
          operation: "write",
          errnoCode: (error as NodeJS.ErrnoException).code,
        }),
        items_count: items.length,
      });
    }
    throw error;
  }
}

/**
 * Atomically update queue files with a transform function
 * Ensures no race condition between read and write
 *
 * The entire read-modify-write cycle happens inside a lock
 */
export async function atomicUpdateQueue<T>(
  queueFile: string,
  transform: (currentItems: T[]) => T[],
): Promise<void> {
  try {
    await withFileLock(queueFile, async () => {
      // Read current state (inside lock)
      const currentItems = await readJsonl<T>(queueFile);

      // Apply transformation
      const newItems = transform(currentItems);

      // Write back (still inside lock)
      await ensureDirectory(dirname(queueFile));
      const content =
        newItems.map((item) => JSON.stringify(item)).join("\n") + (newItems.length > 0 ? "\n" : "");
      await writeFile(queueFile, content, "utf8");

      logger.debug(
        `Atomically updated queue file: ${queueFile} (${currentItems.length}  ${newItems.length} items)`,
      );
    });
  } catch (error) {
    logger.error(`Failed to atomically update queue file ${queueFile}:`, error);
    throw error;
  }
}

/**
 * Clear a queue file (delete it)
 */
export async function clearQueue(queueFile: string): Promise<void> {
  try {
    await withFileLock(queueFile, async () => {
      await deleteFile(queueFile);
      logger.debug(`Cleared queue file: ${queueFile}`);
    });
  } catch (error) {
    logger.error(`Failed to clear queue file ${queueFile}:`, error);
    throw error;
  }
}

/**
 * Get statistics about queued items
 */
export async function getQueueStats(): Promise<{
  events: number;
  sessions: number;
  messages: number;
}> {
  try {
    // Ensure queue directory exists before reading (defensive programming)
    await ensureDirectory(QUEUE_DIR);

    const [events, sessions, messages] = await Promise.all([
      countLines(EVENTS_QUEUE_FILE),
      countLines(SESSIONS_QUEUE_FILE),
      countLines(MESSAGES_QUEUE_FILE),
    ]);

    return { events, sessions, messages };
  } catch (error) {
    logger.error("Failed to get queue stats:", error);
    return { events: 0, sessions: 0, messages: 0 };
  }
}

/**
 * Ensure queue directory exists (call on initialization)
 */
export async function initializeQueue(): Promise<void> {
  try {
    await ensureDirectory(QUEUE_DIR);
    logger.debug("Queue directory initialized");
  } catch (error) {
    logger.error("Failed to initialize queue directory:", error);
    throw error;
  }
}

/**
 * Detailed queue statistics including session readiness breakdown
 */
export interface DetailedQueueStats {
  events: number;
  sessions: {
    total: number;
    readyToSync: number; // Sessions with >= MIN_MESSAGES_PER_SESSION messages
    waitingForMessages: number; // Sessions with < MIN_MESSAGES_PER_SESSION messages
  };
  messages: {
    total: number;
    inReadySessions: number; // Messages belonging to sessions ready to sync
    inWaitingSessions: number; // Messages belonging to sessions waiting for more messages
  };
}

/**
 * Get detailed statistics about queued items including session readiness breakdown
 * This helps explain why some sessions are pending (waiting for more messages)
 */
export async function getDetailedQueueStats(): Promise<DetailedQueueStats> {
  try {
    // Ensure queue directory exists before reading
    await ensureDirectory(QUEUE_DIR);

    // Read all data
    const [events, sessions, messages] = await Promise.all([
      countLines(EVENTS_QUEUE_FILE),
      readJsonl<ClaudeExtractedSession>(SESSIONS_QUEUE_FILE),
      readJsonl<ClaudeExtractedMessage>(MESSAGES_QUEUE_FILE),
    ]);

    // Count max message index per session (0-based, so index 2 means 3 messages)
    const maxMessageIndexBySession = new Map<string, number>();
    for (const message of messages) {
      if (!message.session_id || message.message_index === undefined) continue;
      const currentMax = maxMessageIndexBySession.get(message.session_id) ?? -1;
      if (message.message_index > currentMax) {
        maxMessageIndexBySession.set(message.session_id, message.message_index);
      }
    }

    // Categorize sessions
    let readyToSync = 0;
    let waitingForMessages = 0;
    const readySessionIds = new Set<string>();

    for (const session of sessions) {
      if (!session.id) continue;
      const maxIndex = maxMessageIndexBySession.get(session.id) ?? -1;
      // message_index is 0-based, so maxIndex >= 2 means at least 3 messages
      if (maxIndex >= MIN_MESSAGES_PER_SESSION - 1) {
        readyToSync++;
        readySessionIds.add(session.id);
      } else {
        waitingForMessages++;
      }
    }

    // Count messages by session readiness
    let messagesInReadySessions = 0;
    let messagesInWaitingSessions = 0;
    for (const message of messages) {
      if (!message.session_id) continue;
      if (readySessionIds.has(message.session_id)) {
        messagesInReadySessions++;
      } else {
        messagesInWaitingSessions++;
      }
    }

    return {
      events,
      sessions: {
        total: sessions.length,
        readyToSync,
        waitingForMessages,
      },
      messages: {
        total: messages.length,
        inReadySessions: messagesInReadySessions,
        inWaitingSessions: messagesInWaitingSessions,
      },
    };
  } catch (error) {
    logger.error("Failed to get detailed queue stats:", error);
    return {
      events: 0,
      sessions: { total: 0, readyToSync: 0, waitingForMessages: 0 },
      messages: { total: 0, inReadySessions: 0, inWaitingSessions: 0 },
    };
  }
}
