/**
 * Session management
 *
 * Handles session persistence and validation.
 * Token refresh is handled automatically by Supabase client (see client.ts).
 */

import { readFile, unlink, writeFile } from "node:fs/promises";
import { dirname } from "node:path";
import { AUTH_SESSION_LOAD_FAILED, AUTH_SESSION_SAVE_FAILED } from "../analytics/events.js";
import { captureException } from "../analytics/index.js";
import { buildFileSystemProperties } from "../analytics/properties.js";
import { SESSION_FILE } from "../config/constants.js";
import { ensureDirectory } from "../utils/fs-utils.js";
import { logger } from "../utils/logger.js";

export interface AuthSession {
  accessToken: string;
  refreshToken: string;
  refreshTokenExpiresAt?: number; // Refresh token expiration (milliseconds) - optional for backward compatibility
  userId: string;
  email: string;
  workspaceId?: string;
  workspaceName?: string;
}

/**
 * Load session from file (raw read, no refresh logic)
 * Used by Supabase client for initial session restoration and onAuthStateChange handler
 */
export async function loadSessionFile(): Promise<AuthSession | null> {
  try {
    const content = await readFile(SESSION_FILE, "utf-8");
    const session = JSON.parse(content) as AuthSession;

    // Validate session structure
    if (!session.accessToken || !session.refreshToken || !session.userId || !session.email) {
      logger.warn("Invalid session structure, clearing session");
      await clearSession();
      return null;
    }

    return session;
  } catch (error) {
    if ((error as NodeJS.ErrnoException).code === "ENOENT") {
      // File doesn't exist - not an error
      return null;
    }
    logger.error("Failed to load session file", error);
    if (error instanceof Error) {
      captureException(error, AUTH_SESSION_LOAD_FAILED, "session-manager", {
        ...buildFileSystemProperties({
          filePath: SESSION_FILE,
          operation: "read",
          errnoCode: (error as NodeJS.ErrnoException).code,
        }),
      });
    }
    return null;
  }
}

/**
 * Load session from file with basic validation
 * Token refresh is handled by Supabase client automatically
 */
export async function loadSession(): Promise<AuthSession | null> {
  return loadSessionFile();
}

/**
 * Save session to file with secure permissions
 */
export async function saveSession(session: AuthSession): Promise<void> {
  try {
    // Ensure directory exists
    await ensureDirectory(dirname(SESSION_FILE));

    // Write session file
    await writeFile(SESSION_FILE, JSON.stringify(session, null, 2), {
      encoding: "utf-8",
      mode: 0o600, // Readable/writable by owner only
    });

    logger.info("Session saved successfully");
  } catch (error) {
    logger.error("Failed to save session", error);
    if (error instanceof Error) {
      captureException(error, AUTH_SESSION_SAVE_FAILED, "session-manager", {
        ...buildFileSystemProperties({
          filePath: SESSION_FILE,
          operation: "write",
          errnoCode: (error as NodeJS.ErrnoException).code,
        }),
      });
    }
    throw error;
  }
}

/**
 * Clear session file
 */
export async function clearSession(): Promise<void> {
  try {
    await unlink(SESSION_FILE);
    logger.info("Session cleared successfully");
  } catch (error) {
    if ((error as NodeJS.ErrnoException).code === "ENOENT") {
      // File doesn't exist - not an error
      return;
    }
    logger.error("Failed to clear session", error);
    throw error;
  }
}

/**
 * Check if user is currently authenticated
 * Note: Does not check if access token is expired (Supabase handles refresh)
 */
export async function isAuthenticated(): Promise<boolean> {
  const session = await loadSessionFile();
  if (!session) return false;

  // Only check refresh token expiry - access token handled by Supabase
  if (session.refreshTokenExpiresAt && session.refreshTokenExpiresAt < Date.now()) {
    return false;
  }

  return true;
}

/**
 * Get current session if available
 * Token refresh is handled automatically by Supabase client
 */
export async function getValidSession(): Promise<AuthSession | null> {
  const session = await loadSessionFile();
  if (!session) {
    logger.debug("getValidSession: No session found");
    return null;
  }

  // Check if refresh token is expired (can't recover from this)
  if (session.refreshTokenExpiresAt && session.refreshTokenExpiresAt < Date.now()) {
    logger.warn("getValidSession: Refresh token expired, user must re-authenticate");
    await clearSession();
    return null;
  }

  return session;
}
