/**
 * Claude Code instance PID tracking
 *
 * Maintains a registry of running Claude Code instance PIDs to determine
 * when the daemon should shut down
 */

import { readFile, rename, unlink, writeFile } from "node:fs/promises";
import { CLAUDE_INSTANCES_FILE } from "../config/constants.js";
import { isProcessRunning } from "./daemon-manager.js";
import { withFileLock } from "./file-lock.js";
import { logger } from "./logger.js";

interface ClaudeInstancesRegistry {
  pids: number[];
}

/**
 * Get tracked PIDs from registry file
 * Handles missing or corrupted files gracefully
 */
export async function getTrackedPids(): Promise<number[]> {
  try {
    const data = await readFile(CLAUDE_INSTANCES_FILE, "utf-8");
    const registry: ClaudeInstancesRegistry = JSON.parse(data);

    if (!Array.isArray(registry.pids)) {
      logger.warn("Claude instances file has invalid format, treating as empty");
      return [];
    }

    return registry.pids;
  } catch (error) {
    // File doesn't exist or is corrupted - treat as empty
    if (error instanceof Error && "code" in error) {
      const code = (error as NodeJS.ErrnoException).code;
      if (code === "ENOENT") {
        // File doesn't exist yet
        return [];
      }
    }
    logger.warn("Failed to read Claude instances file, treating as empty:", error);
    return [];
  }
}

/**
 * Write PIDs to registry file (atomic write - corruption safe)
 */
async function writeTrackedPids(pids: number[]): Promise<void> {
  const registry: ClaudeInstancesRegistry = { pids };
  const content = JSON.stringify(registry, null, 2);

  const tempFile = `${CLAUDE_INSTANCES_FILE}.tmp`;

  try {
    await writeFile(tempFile, content, "utf-8");
    await rename(tempFile, CLAUDE_INSTANCES_FILE);
  } catch (error) {
    logger.error("Failed to write Claude instances file:", error);
    // Clean up temp file if it exists
    try {
      await unlink(tempFile);
    } catch {
      // Ignore cleanup errors
    }
    throw error;
  }
}

/**
 * Register a Claude Code instance PID
 * Adds to registry if not already present
 */
export async function registerClaudePid(pid: number): Promise<void> {
  try {
    await withFileLock(CLAUDE_INSTANCES_FILE, async () => {
      const pids = await getTrackedPids();

      if (!pids.includes(pid)) {
        pids.push(pid);
        await writeTrackedPids(pids);
        logger.debug(`Registered Claude instance PID: ${pid}`);
      } else {
        logger.debug(`Claude instance PID ${pid} already registered`);
      }
    });
  } catch (error) {
    logger.error("Failed to register Claude PID:", error);
  }
}

/**
 * Unregister a Claude Code instance PID
 * Removes from registry
 */
export async function unregisterClaudePid(pid: number): Promise<void> {
  try {
    await withFileLock(CLAUDE_INSTANCES_FILE, async () => {
      const pids = await getTrackedPids();
      const filtered = pids.filter((p) => p !== pid);

      // Only write if something changed
      if (filtered.length !== pids.length) {
        await writeTrackedPids(filtered);
        logger.debug(`Unregistered Claude instance PID: ${pid}`);
      } else {
        logger.debug(`Claude instance PID ${pid} was not registered`);
      }
    });
  } catch (error) {
    logger.error("Failed to unregister Claude PID:", error);
  }
}

/**
 * Get alive PIDs and prune dead ones from registry
 * Returns list of currently alive PIDs
 * Uses file locking to prevent race conditions with registerClaudePid/unregisterClaudePid
 */
export async function getAlivePidsAndPrune(): Promise<number[]> {
  try {
    return await withFileLock(CLAUDE_INSTANCES_FILE, async () => {
      const pids = await getTrackedPids();
      const alivePids = pids.filter((pid) => isProcessRunning(pid));

      // Write back pruned list if we removed any dead PIDs
      if (alivePids.length !== pids.length) {
        await writeTrackedPids(alivePids);
        const removedCount = pids.length - alivePids.length;
        logger.debug(`Pruned ${removedCount} dead Claude instance PID(s)`);
      }

      return alivePids;
    });
  } catch (error) {
    logger.error("Failed to prune dead PIDs:", error);
    return [];
  }
}

/**
 * Check if any Claude Code instances are running
 * Automatically prunes dead PIDs from registry
 */
export async function isAnyClaudeRunning(): Promise<boolean> {
  const alivePids = await getAlivePidsAndPrune();
  return alivePids.length > 0;
}
