import { z } from "zod";

export const authEvents = {
  userSignedUp: {
    name: "User Signed Up",
    schema: z.object({
      email: z.email().optional(),
      method: z.union([
        z.literal("email"),
        z.literal("google"),
        z.literal("github"),
        z.literal("unknown"),
      ]),
    }),
  },
  firstDataSent: {
    name: "First Data Sent",
    schema: z.object({
      email: z.email().optional(),
      workspaceId: z.uuid(),
      workspaceName: z.string(),
    }),
  },
  cliSignedIn: {
    name: "CLI Signed In",
    schema: z.object({
      plugin_version: z.string(),
      claude_code_version: z.string().optional(),
      node_version: z.string(),
      os_platform: z.string(),
      os_version: z.string(),
    }),
  },
} as const;
