/**
 * Message parsing logic for Claude JSONL files
 *
 * Uses streaming to handle large conversation files without loading
 * the entire file into memory.
 */

import { createReadStream } from "node:fs";
import { createInterface } from "node:readline";
import type { ClaudeJSONLEntry, ToolUse } from "../types/extractors.js";
import type { ClaudeExtractedMessage } from "../types/index.js";
import {
  applyMessageFilter,
  type FilteringState,
  restoreFilteringState,
} from "../utils/command-filters.js";
import { logger } from "../utils/logger.js";
import {
  extractTextContent,
  extractToolUse,
  extractToolUseResult,
  logDiff,
} from "./extraction-utils.js";

/**
 * Extract only NEW messages and tool uses from JSONL file (incremental extraction)
 *
 * Uses streaming to process the file line-by-line without loading it entirely
 * into memory. This handles large conversation files efficiently.
 *
 * Race condition handling: Only advances lastReadLine to the last successfully
 * parsed line. If a partial line is encountered (e.g., from concurrent writes
 * by Claude Code), it will be re-read on the next extraction.
 *
 * @param filePath - Path to conversation.jsonl file
 * @param sessionId - Session identifier
 * @param lastReadLine - Last line number that was read (0-indexed)
 * @param startingMessageIndex - Index to start numbering messages from (for incremental extraction)
 * @returns New messages, tool uses, updated line number, and last message index
 */
export async function extractNewMessagesFromFile(
  filePath: string,
  sessionId: string,
  lastReadLine = 0,
  startingMessageIndex = 0,
): Promise<{
  messages: ClaudeExtractedMessage[];
  toolUses: ToolUse[];
  newLastReadLine: number;
  lastMessageIndex: number;
  totalLines: number;
}> {
  const messages: ClaudeExtractedMessage[] = [];
  const toolUses: ToolUse[] = [];

  // Sliding window of recent lines for state restoration.
  // We only need the last 10 lines before lastReadLine to restore filtering state.
  // This keeps memory bounded regardless of file size or lastReadLine position.
  const LOOKBACK_WINDOW = 10;
  const recentLines: string[] = [];

  try {
    logger.debug(`Incremental extraction for ${sessionId}: reading from line ${lastReadLine}`);

    const stream = createReadStream(filePath, { encoding: "utf-8" });
    const rl = createInterface({ input: stream, crlfDelay: Number.POSITIVE_INFINITY });

    let lineNumber = 0;
    let lastSuccessfulLine = lastReadLine - 1; // Track last successfully parsed line
    let messageCounter = startingMessageIndex;
    let filteringState: FilteringState = {
      filteringAssistantResponses: false,
      lastWasZestCommand: false,
    };
    let filteringStateInitialized = false;

    // Use for-await to handle async operations cleanly
    for await (const line of rl) {
      const trimmedLine = line.trim();
      if (!trimmedLine) {
        lineNumber++;
        continue;
      }

      // Collect recent lines for state restoration (sliding window)
      if (lineNumber < lastReadLine) {
        recentLines.push(trimmedLine);
        // Keep only the last LOOKBACK_WINDOW lines
        if (recentLines.length > LOOKBACK_WINDOW) {
          recentLines.shift();
        }
        lineNumber++;
        continue;
      }

      // Initialize filtering state from recent lines (once, when we reach new lines)
      if (!filteringStateInitialized) {
        filteringState = restoreFilteringState(recentLines, recentLines.length);
        recentLines.length = 0; // Free memory
        filteringStateInitialized = true;

        // Log how many new lines we're about to process
        logger.info(
          `Processing new lines for session ${sessionId} starting from line ${lastReadLine + 1}`,
        );
      }

      // Process new line
      try {
        const entry: ClaudeJSONLEntry = JSON.parse(trimmedLine);

        // Skip entries without message field
        if (entry.message) {
          const role = entry.message.role;
          const content = entry.message.content;
          // Extract user or assistant messages
          if ((role === "user" || role === "assistant") && content) {
            const textContent = extractTextContent(content);

            if (textContent) {
              // Apply state-based filtering logic
              const filterResult = applyMessageFilter(role, textContent, filteringState);
              filteringState = filterResult.newState;

              if (!filterResult.shouldFilter) {
                // Build metadata: claude_uuid for all messages, model for assistant messages
                const metadata: Record<string, string> = {};
                if (entry.uuid) metadata.claude_uuid = entry.uuid;
                if (role === "assistant" && entry.message.model) {
                  metadata.modelName = entry.message.model;
                }

                messages.push({
                  id: entry.uuid,
                  session_id: sessionId,
                  role: role,
                  content: textContent,
                  created_at: entry.timestamp || new Date().toISOString(),
                  message_index: messageCounter,
                  metadata: Object.keys(metadata).length > 0 ? metadata : null,
                });
                messageCounter++;

                logger.debug(
                  `Extracted ${role} message at line ${lineNumber + 1}: ${textContent.substring(0, 50)}...`,
                );
              }
            }
          }
        }

        // Extract tool use results with diffs (primary source)
        // This contains the actual results and diffs after tool execution
        if (entry.toolUseResult) {
          const toolUseWithDiff = extractToolUseResult(entry, sessionId);
          if (toolUseWithDiff) {
            toolUses.push(toolUseWithDiff);

            logger.debug(
              `Extracted tool result at line ${lineNumber + 1}: ${toolUseWithDiff.file_path}`,
            );

            logDiff(toolUseWithDiff.file_path || "", toolUseWithDiff.diff);
          }
        }
        // Fallback: Check message content array for tool uses without results
        // This handles Bash commands and other tools that don't have toolUseResult
        else if (entry.message && Array.isArray(entry.message.content)) {
          for (const contentBlock of entry.message.content) {
            if (contentBlock.type === "tool_use") {
              const extractedToolUses = await extractToolUse(
                contentBlock,
                sessionId,
                entry.timestamp,
              );
              for (const toolUse of extractedToolUses) {
                toolUses.push(toolUse);
                logger.debug(
                  `Extracted tool use at line ${lineNumber + 1}: ${toolUse.tool_name} on ${toolUse.file_path}`,
                );
              }
            }
          }
        }

        // Only update lastSuccessfulLine on successful parse
        lastSuccessfulLine = lineNumber;
      } catch (parseError) {
        // Don't update lastSuccessfulLine - partial line will be re-read next time
        logger.debug(
          `Failed to parse JSONL line ${lineNumber + 1}, will retry next extraction: ${trimmedLine.substring(0, 100)}...`,
          parseError,
        );
      }

      lineNumber++;
    }

    // Handle case where there were no new lines to process
    if (!filteringStateInitialized && lineNumber <= lastReadLine) {
      logger.debug(`No new lines for ${sessionId}: total=${lineNumber}, lastRead=${lastReadLine}`);
      return {
        messages,
        toolUses,
        newLastReadLine: lastReadLine,
        // No new messages extracted. Return startingMessageIndex - 1 to indicate the previous last index.
        // For new sessions (startingMessageIndex=0), this returns -1 meaning "no messages indexed yet".
        lastMessageIndex: startingMessageIndex - 1,
        totalLines: lineNumber,
      };
    }

    logger.info(
      `Incremental extraction complete: ${messages.length} messages, ${toolUses.length} tool uses`,
    );

    return {
      messages,
      toolUses,
      newLastReadLine: lastSuccessfulLine + 1, // Start after last successful line
      lastMessageIndex: messageCounter - 1, // Last assigned index
      totalLines: lineNumber,
    };
  } catch (error) {
    logger.error(`Failed to stream conversation file ${filePath}:`, error);
    return {
      messages,
      toolUses,
      newLastReadLine: lastReadLine,
      lastMessageIndex: startingMessageIndex - 1, // Error case, return previous last index
      totalLines: lastReadLine,
    };
  }
}
