/**
 * Git repository utilities
 *
 * Extracts project information from git repositories
 */

import { exec, execSync } from "node:child_process";
import * as path from "node:path";
import { promisify } from "node:util";
import { v5 as uuidv5 } from "uuid";

const execAsync = promisify(exec);

export interface ProjectInfo {
  projectId: string;
  projectName: string;
}

const UNKNOWN_PROJECT: ProjectInfo = {
  projectId: "unknown",
  projectName: "unknown",
} as const;

/**
 * Fixed namespace UUID for generating project IDs
 * Using the same namespace as VS Code extension for consistency
 */
const PROJECT_ID_NAMESPACE = "e1f3b3c4-0b7a-4c1e-8a7b-9f3c0e1d2a3b";

/**
 * Generate a deterministic project ID from a string using UUID v5
 * This ensures compatibility with database UUID type requirements
 */
function generateProjectId(input: string): string {
  return uuidv5(input, PROJECT_ID_NAMESPACE);
}

/**
 * Extract org/repo name from a git remote URL
 *
 * Handles common URL formats:
 * - HTTPS: https://github.com/org/repo.git -> org/repo
 * - SSH:   git@github.com:org/repo.git    -> org/repo
 * - Plain: https://github.com/org/repo    -> org/repo
 *
 * Falls back to just the repo name if org cannot be determined.
 */
function extractNameFromRemoteUrl(remoteUrl: string): string | null {
  // Try org/repo format first (covers both HTTPS and SSH)
  // HTTPS: ...github.com/org/repo.git  - [/:] matches the / before org
  // SSH:   ...github.com:org/repo.git  - [/:] matches the : before org
  // The org part must not contain dots to avoid matching domain names (e.g., github.com/repo)
  const orgRepoMatch = remoteUrl.match(/[/:]([^/:.]+\/[^/]+?)(\.git)?$/);
  if (orgRepoMatch?.[1]) {
    return orgRepoMatch[1];
  }

  // Fallback: just the repo name (e.g., bare URLs without org)
  const repoMatch = remoteUrl.match(/\/([^/]+?)(\.git)?$/);
  if (repoMatch?.[1]) {
    return repoMatch[1];
  }

  return null;
}

/**
 * Extract project name from git repository
 * Tries multiple strategies in order:
 * 1. Git remote origin URL (e.g., github.com/org/repo-name -> org/repo-name)
 * 2. Git repository root directory name
 * 3. Working directory name
 */
function extractProjectName(workingDirectory: string): string | null {
  try {
    // Strategy 1: Try to get project name from git remote origin URL
    try {
      const remoteUrl = execSync("git config --get remote.origin.url", {
        cwd: workingDirectory,
        encoding: "utf-8",
        stdio: ["pipe", "pipe", "pipe"],
        timeout: 5000,
      }).trim();

      if (remoteUrl) {
        const name = extractNameFromRemoteUrl(remoteUrl);
        if (name) {
          return name;
        }
      }
    } catch {
      // Remote origin might not exist, continue to next strategy
    }

    // Strategy 2: Get git repository root directory name
    try {
      const repoRoot = execSync("git rev-parse --show-toplevel", {
        cwd: workingDirectory,
        encoding: "utf-8",
        stdio: ["pipe", "pipe", "pipe"],
        timeout: 5000,
      }).trim();

      if (repoRoot) {
        return path.basename(repoRoot);
      }
    } catch {
      // Not a git repository or command failed
    }

    // Strategy 3: Use working directory name as fallback
    return path.basename(workingDirectory);
  } catch {
    return null;
  }
}

/**
 * Check if a directory is inside a git repository
 */
function isGitRepository(workingDirectory: string): boolean {
  try {
    execSync("git rev-parse --is-inside-work-tree", {
      cwd: workingDirectory,
      encoding: "utf-8",
      stdio: ["pipe", "pipe", "pipe"],
      timeout: 5000,
    });
    return true;
  } catch {
    return false;
  }
}

async function isGitRepositoryAsync(workingDirectory: string): Promise<boolean> {
  try {
    await execAsync("git rev-parse --is-inside-work-tree", {
      cwd: workingDirectory,
      encoding: "utf-8",
      timeout: 5000,
    });
    return true;
  } catch {
    return false;
  }
}

async function extractProjectNameAsync(workingDirectory: string): Promise<string | null> {
  try {
    // Strategy 1: Try to get project name from git remote origin URL
    try {
      const { stdout } = await execAsync("git config --get remote.origin.url", {
        cwd: workingDirectory,
        encoding: "utf-8",
        timeout: 5000,
      });
      const remoteUrl = stdout.trim();

      if (remoteUrl) {
        const name = extractNameFromRemoteUrl(remoteUrl);
        if (name) {
          return name;
        }
      }
    } catch {
      // Remote origin might not exist, continue to next strategy
    }

    // Strategy 2: Get git repository root directory name
    try {
      const { stdout } = await execAsync("git rev-parse --show-toplevel", {
        cwd: workingDirectory,
        encoding: "utf-8",
        timeout: 5000,
      });
      const repoRoot = stdout.trim();

      if (repoRoot) {
        return path.basename(repoRoot);
      }
    } catch {
      // Not a git repository or command failed
    }

    // Strategy 3: Use working directory name as fallback
    return path.basename(workingDirectory);
  } catch {
    return null;
  }
}

/**
 * Get project information from a git repository
 *
 * @param workingDirectory - The directory to extract project info from
 * @returns ProjectInfo with projectId and projectName, or "unknown" values if git is not accessible
 */
export async function getProjectInfo(workingDirectory: string): Promise<ProjectInfo> {
  try {
    if (!(await isGitRepositoryAsync(workingDirectory))) {
      return UNKNOWN_PROJECT;
    }

    const projectName = await extractProjectNameAsync(workingDirectory);

    if (!projectName) {
      return UNKNOWN_PROJECT;
    }

    const absolutePath = path.resolve(workingDirectory);
    const projectId = generateProjectId(absolutePath);

    return {
      projectId,
      projectName,
    };
  } catch {
    return UNKNOWN_PROJECT;
  }
}

/**
 * Synchronous version of getProjectInfo for environments where async is not convenient
 */
export function getProjectInfoSync(workingDirectory: string): ProjectInfo {
  try {
    if (!isGitRepository(workingDirectory)) {
      return UNKNOWN_PROJECT;
    }

    const projectName = extractProjectName(workingDirectory);

    if (!projectName) {
      return UNKNOWN_PROJECT;
    }

    const absolutePath = path.resolve(workingDirectory);
    const projectId = generateProjectId(absolutePath);

    return {
      projectId,
      projectName,
    };
  } catch {
    return UNKNOWN_PROJECT;
  }
}
