/**
 * Analytics module for error tracking with PostHog
 *
 * Uses @zest/analytics package for PostHog integration.
 * All analytics operations are non-blocking and fail silently
 * to avoid disrupting the main plugin functionality.
 */

import { release } from "node:os";
import { createServerAnalytics, type ExceptionContext } from "@zest/analytics";
import type { AuthSession } from "../auth/session-manager.js";
import { loadSession } from "../auth/session-manager.js";
import { POSTHOG_API_KEY } from "../config/constants.js";
import { getClaudeCodeVersion } from "../utils/claude-version.js";
import { logger } from "../utils/logger.js";
import { getPluginVersion } from "../utils/plugin-version.js";
import { type ErrorType, getErrorCategory } from "./events.js";

type AnalyticsClient = ReturnType<typeof createServerAnalytics>;

// Singleton analytics client (lazy initialization)
let analyticsClient: AnalyticsClient | null = null;

// Cached session for user context
let cachedSession: AuthSession | null = null;

/**
 * Get or create the analytics client (singleton)
 * Also loads the session from disk to ensure user context is available
 */
async function getAnalyticsClient(): Promise<AnalyticsClient | null> {
  if (!POSTHOG_API_KEY) {
    return null; // Analytics not configured
  }

  if (!analyticsClient) {
    analyticsClient = createServerAnalytics(POSTHOG_API_KEY);

    // Load session from disk for user context
    // Each process is stateless, so we need to load it fresh each time
    try {
      const session = await loadSession();
      if (session) {
        cachedSession = session;
      }
    } catch (error) {
      // Silently fail - analytics should work even without user context
      logger.debug("Could not load session for analytics context", error);
    }
  }

  return analyticsClient;
}

/**
 * Build standard properties included with every error
 */
function buildStandardProperties(): Record<string, unknown> {
  return {
    plugin_version: getPluginVersion(),
    node_version: process.version,
    os_platform: process.platform,
    os_version: release(),
  };
}

/**
 * Build user context properties from cached session
 */
function buildUserProperties(): Record<string, unknown> {
  if (!cachedSession) {
    return {};
  }

  return {
    user_id: cachedSession.userId,
    email: cachedSession.email,
    workspace_id: cachedSession.workspaceId,
    workspace_name: cachedSession.workspaceName,
  };
}

/**
 * Capture an exception for error tracking
 *
 * This function is non-blocking and fails silently.
 * Analytics errors should never disrupt plugin functionality.
 *
 * @param error - The error to capture
 * @param errorType - Categorized error type for filtering in PostHog
 * @param errorSource - Source location where the error originated (e.g., "auth/authentication")
 * @param additionalProperties - Extra context specific to this error
 */
export async function captureException(
  error: Error,
  errorType: ErrorType,
  errorSource: string,
  additionalProperties?: Record<string, unknown>,
): Promise<void> {
  try {
    const client = await getAnalyticsClient();
    if (!client) {
      return; // Analytics not configured, skip silently
    }

    const context: ExceptionContext = {
      error_type: errorType,
      error_category: getErrorCategory(errorType),
      error_source: `claude-cli-plugin/${errorSource}`,
      ...buildStandardProperties(),
      ...buildUserProperties(),
      ...additionalProperties,
    };

    client.captureException(error, cachedSession?.userId, context);

    logger.debug("Exception captured in PostHog", {
      error_type: errorType,
      error_message: error.message,
    });
  } catch (captureError) {
    // Never let analytics errors crash the plugin
    logger.debug("Failed to capture exception in PostHog", captureError);
  }
}

/**
 * Track a successful CLI sign-in event
 *
 * Captures environment info (plugin version, Claude Code version, Node, OS)
 * for diagnostics. Called once per login to minimize analytics volume.
 *
 * @param userId - The authenticated user's ID
 */
export async function trackCliSignedIn(userId: string): Promise<void> {
  try {
    const client = await getAnalyticsClient();
    if (!client) {
      return;
    }

    const claudeVersion = getClaudeCodeVersion();

    client.track({
      distinctId: userId,
      event: "cliSignedIn",
      properties: {
        plugin_version: getPluginVersion(),
        claude_code_version: claudeVersion,
        node_version: process.version,
        os_platform: process.platform,
        os_version: release(),
      },
    });

    // Flush immediately since login-cli exits after completion
    await shutdownAnalytics();

    logger.debug("CLI sign-in tracked", { userId });
  } catch (error) {
    logger.debug("Failed to track CLI sign-in", error);
  }
}

/**
 * Shutdown the analytics client and flush pending events
 *
 * Call this before process exit to ensure all events are sent.
 */
export async function shutdownAnalytics(): Promise<void> {
  try {
    if (analyticsClient) {
      await analyticsClient.dispose();
      analyticsClient = null;
      logger.debug("Analytics client shut down successfully");
    }
  } catch (error) {
    logger.debug("Error shutting down analytics client", error);
  }
}
